const A = 9301;
const C = 49297;
const M = 233280;
export class MathFunc {
  seed(value) {
    this._result = value;
    this.randSeed();
  }

  randSeed() {
    this._result = (this._result * A + C) % M;
    return this._result;
  }

  number(min, max) {
    let diff = max - min + 1;
    let result = this.randSeed() % diff + min;
    return result;
  }

}