import { Vector2 } from "peng-field";
import { Rectangle } from "./GeometryDefine";
export class TreeNode {
  constructor(x, y, data) {
    this.point = new Vector2(x, y);
    this.data = data;
  }

}
export class QuadTree {
  constructor(boundary, capacity) {
    this.nodes = [];
    this.branchs = [];
    this.divided = false;

    if (!boundary) {
      throw TypeError('boundary is null or undefined');
    }

    if (!(boundary instanceof Rectangle)) {
      throw TypeError('boundary should be a Rectangle');
    }

    if (typeof capacity !== 'number') {
      throw TypeError(`capacity should be a number but is a ${typeof capacity}`);
    }

    if (capacity < 1) {
      throw RangeError('capacity must be greater than 0');
    }

    this.boundary = boundary;
    this.capacity = capacity;
  }

  subdivide() {
    let x = this.boundary.x;
    let y = this.boundary.y;
    let w = this.boundary.w / 2;
    let h = this.boundary.h / 2;
    let branchs = new Array(4);
    let ne = new Rectangle(x + w, y - h, w, h);
    let northeast = new QuadTree(ne, this.capacity);
    branchs[0] = northeast;
    let nw = new Rectangle(x - w, y - h, w, h);
    let northwest = new QuadTree(nw, this.capacity);
    branchs[1] = northwest;
    let se = new Rectangle(x + w, y + h, w, h);
    let southeast = new QuadTree(se, this.capacity);
    branchs[2] = southeast;
    let sw = new Rectangle(x - w, y + h, w, h);
    let southwest = new QuadTree(sw, this.capacity);
    branchs[3] = southwest;
    this.branchs = branchs;
    this.divided = true;
  }

  insert(node) {
    if (!this.boundary.contains(node.point)) {
      return null;
    }

    if (this.nodes) {
      if (this.nodes.length < this.capacity) {
        this.nodes.push(node);
        return this;
      }
    }

    if (!this.divided) {
      this.subdivide();
    }

    let result;

    for (let i = 0; i < 4; i++) {
      result = this.branchs[i].insert(node);
      if (result) return result;
    }

    return null;
  }

  query(range, found) {
    if (!found) {
      found = [];
    }

    if (!range.intersects(this.boundary)) {
      return found;
    }

    if (this.nodes) {
      for (let n of this.nodes) {
        if (range.contains(n.point)) {
          found.push(n);
        }
      }
    }

    if (this.divided) {
      for (let i = 0; i < 4; i++) {
        this.branchs[i].query(range, found);
      }
    }

    return found;
  }

}